@import url('https://fonts.googleapis.com/css2?family=Arimo:wght@500&family=Chivo+Mono:wght@600&family=Kanit:wght@400;500;600&family=Orbitron:wght@700&family=Rock+Salt&family=Russo+One&family=Zen+Dots&display=swap');

* {
    /*overflow: hidden;*/
}

:root {
    --left-bar-width: 0px;
    --left-bar-rotation: 0deg;
    --left-bar-color: #cacaca99;
    --left-bar-color-1: rgb(12, 12, 12);

    --right-bar-width: 0;
    --right-bar-rotation: 0deg;
    --right-bar-color: rgba(208, 208, 208, 0.8);
    --right-bar-color-1: rgb(170 170 170);
}

.scroll-page {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.63);
    bottom: 0;
    width: 0;
    height: 0;
    z-index: 5;
    opacity: 0;
    left: 50%;
    transform: translateX(-50%);
}

.scroll-page-full {
    width: 100%;
    height: 100%;
    opacity: 1;
}

.scroll-page-minimized {
    width: 300px;
    max-width: 100%;
    height: 70px;
    margin-bottom: 20px;
    border-radius: 20px;
    opacity: 1;
    padding: 10px;
    background-color: rgba(75, 75, 75, 0.8);
    z-index: 51;
}

.scroll-page p {
    font-size: .3rem;
    margin-bottom: 10px;
    font-family: 'Kanit', sans-serif;
}

.scroll-page-full p {
    font-size: 2.3rem !important;
    margin-bottom: 50px;
    font-family: 'Kanit', sans-serif;
}

.scroll-page-minimized p {
    font-size: 1.3rem !important;
    margin-bottom: 10px;
    font-family: 'Kanit', sans-serif;
}

.scroll-page-progress-bar-container {
    position: relative;
    left: 0;
    width: 90%;
    height: 10px;
    border: 1px solid #ff2525;
    border-radius: 15px;
}

.scroll-page-progress-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 5s ease infinite;
    border-radius: 15px;
}

.homepage {
    width: 100vw;
    height: max-content;
    /*overflow-y: scroll;*/
    /*overflow-x: hidden;*/
}

.homepage-scroll {
    max-width: 100vw;
    height: 500vh;
    position: relative;
    /*overflow-y: scroll;*/
}

.homepage-scroll-lock {
    overflow-y: hidden !important;
}

.page-container {
    position: fixed;
    height: 100vh;
    width: 100vw;
    max-width: 100vw;
    z-index: 2;
}

/* This is for the left bar */

.square-rotated {
    width: var(--left-bar-width);
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--left-bar-color);
    transform: rotate(var(--left-bar-rotation)) scale(2.5);
    transform-origin: center right;
    z-index: 1;
    overflow: hidden;
    box-shadow: 0 0 10px 0px #00000099;
}

.r-square-rotated {
    width: var(--right-bar-width);
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--right-bar-color);
    transform: rotate(var(--right-bar-rotation)) scale(2.5);
    transform-origin: center left;
    z-index: 1;
    overflow: hidden;
    box-shadow: 0 0 10px 0px #00000099;
}


.square-rotated-1 {
    width: calc(var(--left-bar-width) - 70px);
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--left-bar-color-1);
    transform: rotate(var(--left-bar-rotation)) scale(2.5);
    transform-origin: center right;
    z-index: 1;
    overflow: hidden;
    box-shadow: 0 0 10px 0px #00000099;
}

.r-square-rotated-1 {
    width: calc(var(--right-bar-width) - 40px);
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--right-bar-color-1);
    transform: rotate(var(--right-bar-rotation)) scale(2.5);
    transform-origin: center left;
    z-index: 1;
    overflow: hidden;
    box-shadow: 0 0 10px 0px #00000099;
}

.r-square-rotated-1-back {
    top: 32vh;
    left: -50px;
    width: max-content;
    opacity: .3;
    transform: rotate(calc(var(--left-bar-rotation) * -1));
}

.r-square-rotated-1-back-2 {
    top: 19vh;
    left: -35px;
    opacity: .1;
}

.r-square-rotated-1-back-text, .r-square-rotated-1-back-text-2 {
    font-size: 8rem;
    font-family: 'Russo One', sans-serif;
}

.r-square-rotated-1-back-text-2 {
    color: #d0d0d0;
}

.square-rotated-1-light-bar {
    height: 100%;
    width: 3px;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 100vh 100vh;
    position: absolute;
    opacity: 1;
    right: 0;
    z-index: 2;
    animation: glitchOpacity 3s forwards, gradient 15s ease infinite;
}

.square-rotated-1-text, .square-rotated-1-text-2, .square-rotated-1-text-3 {
    position: absolute;
    right: 20px;
    color: white;
    top: 40vh;
    transform: rotate(-90deg);
    font-family: 'Zen Dots', cursive;
    font-size: clamp(1rem, 2vh, 1.6rem);
    transform-origin: right;
}

.square-rotated-1-text-change-top {
    top: 0 !important;
}

.square-rotated-1-text-change-bottom {
    top: 80vh !important;
}

.square-rotated-1-text-change-rotated {
    top: 50vh;
    transform: rotate(90deg);
}

.square-rotated-1-text-2 {
    top: 80vh;
}

.square-rotated-1-text-2-change {
    top: 40vh;
}

.square-rotated-1-text-2-change-rotated {
    top: 50vh;
    transform: rotate(90deg);
}

.square-rotated-1-text-3 {
    top: 0;
}

.square-rotated-1-text-3-change {
    top: 40vh;
}

.square-rotated-1-text-3-change-rotated {
    top: 50vh;
    transform: rotate(90deg);
}

.text-transition {
    transition: top 2000ms cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
    transition-timing-function: cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
}

.page-transition {
    transition: top 2000ms cubic-bezier(0.795, 0.000, 0.345, 0.995), transform 2000ms cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
    transition-timing-function: cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
}

.square-rotated-1-page-1, .square-rotated-1-page-2, .square-rotated-1-page-3 {
    position: absolute;
    right: 65px;
    color: rgba(115, 115, 115, 0.45);
    font-family: 'Zen Dots', cursive;
    font-size: 3rem;
    transform: rotate(calc(var(--left-bar-rotation) * -1)) scale(10);
}

.square-rotated-1-page-1 {
    top: 46vh;
}

.square-rotated-1-page-1-previous {
    top: 95vh;
}

.square-rotated-1-page-1-next {
    top: 5vh;
}

.square-rotated-1-page-3 {
    top: 5vh;
}

.square-rotated-1-page-3-change {
    top: 46vh;
}

.square-rotated-1-page-2 {
    top: 95vh;
}

.square-rotated-1-page-2-change {
    top: 46vh;
}

/* This is for left bar content */

.page2-square-content-title {
    display: flex;
    align-items: end;
    /*color: orange;*/
    font-family: 'Kanit', sans-serif;
    font-size: clamp(1.5rem, 5vw, 2.5rem);
    font-weight: 1000;
    margin: 0;
    --bg-size: 400%;
    --color-one: hsl(15 90% 55%);
    --color-two: hsl(40 95% 55%);
    background: linear-gradient(
            90deg,
            var(--color-one),
            var(--color-two),
            var(--color-one)
    ) 0 0 / var(--bg-size) 100%;
    color: transparent;
    background-clip: text;
    animation: gradient 5s ease infinite;
}

.page2-square-content-title p {
    font: .9rem 'Kanit', sans-serif;
    font-weight: 50;
    margin: 8px 5px;
}


.page2-light-bar {
    bottom: 0;
}


@keyframes glitchOpacity {
    0% {
        opacity: 1;
    }

    19% {
        opacity: 1;
    }
    20% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }
    29% {
        opacity: 1;
    }
    30% {
        opacity: 0;
    }

    39% {
        opacity: 0;
    }
    40% {
        opacity: 1;
    }

    55% {
        opacity: 1;
    }
    60% {
        opacity: 0;
    }
    69% {
        opacity: 0;
    }
    70% {
        opacity: 1;
    }
    79% {
        opacity: 1;
    }
    80% {
        opacity: 0;
    }
    85% {
        opacity: 0;
    }
    86% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.square-content {
    position: absolute;
    top: 0;
    right: 0;
    width: calc(var(--right-bar-width) - 70px);
    height: 80vh;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: rotate(13deg);
    overflow: hidden;
}

.square-content-container {
    transform: rotate(-13deg) scale(1.2);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.square-content-aboutme-container {
    position: absolute;
    bottom: 200px;
    right: 200px;
    width: 600px;
    height: max-content;
    z-index: 2;
}

.square-content-aboutme {
    position: relative;
    width: calc(100% + 150px);
    background-color: #939393;
    z-index: 2;
    overflow: hidden;
}

.square-content-aboutme-text {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'Zen Dots', cursive;
    font-size: clamp(1rem, 2vh, 1.6rem);
    overflow: hidden;
    background-color: #505050;
    z-index: 3;
}

.square-content-aboutme-text:hover {
    filter: opacity(0.5);
    cursor: pointer;
    user-select: none;
}

.square-content-aboutme-text-2-text {
    padding: 60px 10px 30px 10px;
    text-align: justify;
}

.output-image {
    max-height: 200px;
}

.aboutme-expand {
    height: 200px;
}

.aboutme-collapse {
    height: 100px;
}


.page2-square-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 100px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 20px;
    align-items: center;
}

.page3-content {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 170px;
    width: 0;
    background-color: black;
    z-index: 2;
    overflow-x: hidden;
    overflow-y: auto;
}

.page3-content-show {
    width: 300px;
}

.page3-content-hide {
    width: 0;
}

.page3-content-project {
    width: 300px;
    height: 100px;
    position: relative;
    user-select: none;
    cursor: pointer;
}


.page3-content-project-image {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position-x: 50%;
    background-position-y: 40%;
    background-repeat: no-repeat;
    filter: grayscale(1) opacity(0.4);
}

.page3-content-project-name {
    opacity: 1;
    transform: translateY(0px);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.page3-content-project-description {
    opacity: 0;
    transform: translateY(50px);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: .9rem;
}

.page3-content-project:hover > .page3-content-project-image {
    filter: grayscale(0) opacity(.5);
    background-position-y: 50%;
}

.page3-content-project:hover .page3-content-project-name {
    opacity: 0;
    transform: translateY(-50px);
}

.page3-content-project:hover .page3-content-project-description {
    opacity: 1;
    transform: translateY(0px);
}

.page3-content-project-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'Zen Dots', cursive;
    font-size: clamp(1rem, 2vh, 1.6rem);
    overflow: hidden;
    z-index: 3;
    padding: 3px 8px;
}

.page3-content-project-show {
    animation: forwards page3-project-flydown 1s ease-in-out;
}

.page3-content-project-hide {
    opacity: 0;
}

@keyframes page3-project-flydown {
    0% {
        transform: translateY(-100%);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.square-content-avatar {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    margin-bottom: 20px;
}

.square-content-text {
    display: flex;
    align-items: center;
    font-size: 1.8rem;
}

.page2-square-content-languages {
    width: clamp(430px, 40vw, 540px);
    flex-wrap: wrap;
    padding: 30px 10px;
    overflow-y: auto;
    overflow-x: hidden;
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    display: grid;
    grid-template-columns: repeat(auto-fill, 240px);
    grid-gap: 10px;
    justify-content: end;
    position: absolute;
    right: 0;
    z-index: 5;
    top: 0;
    bottom: 170px;
}

.page2-square-content-language {
    width: 230px;
    height: 85px;
    padding: 25px 0 25px 35px;
    display: flex;
    align-items: center;
}

.page2-square-content-language:hover {
    transform: scale(.9);
    transition: .3s;
}

.page2-square-content-language img {
    height: 60px;
    object-fit: cover;
}

.page2-square-content-language div {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.page2-square-content-language-text {
    color: black;
    font-family: 'Chivo Mono', monospace;
}

@keyframes hueRotate {
    0% {
        filter: hue-rotate(0deg);
    }
    100% {
        filter: hue-rotate(360deg);
    }
}

.square-content-text-n span {
    margin-left: 5px;
}

.square-content-text-glitch {
    margin-left: 5px;
}

.square-content-hide {
    right: -20vw;
    opacity: 0;
}

.page2-square-content-hide {
    opacity: 0;
    top: -20vw;
}

.square-content-show {
    opacity: 1;
}

.flyleft-1 {
    opacity: 1;
    margin-left: 0;
}

.flyleft-2 {
    opacity: 0;
    margin-left: -150px;
}

.flyright-1 {
    opacity: 1;
    margin-left: 0;
}

.flyright-2 {
    opacity: 0;
    margin-left: 150px;
}

.flyup-1 {
    opacity: 1;
    margin-top: 0;
}

.flyup-2 {
    opacity: 0;
    margin-top: -150px;
}

.home-page-container {
    position: -webkit-sticky;
    position: -moz-sticky;
    position: -ms-sticky;
    position: -o-sticky;
    position: sticky;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-page-video-background {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.home-page-video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.opacity-video {
    opacity: .7;
}

.flydown-1-m {
    opacity: 1;
    transform: translateY(0px);
}

.flydown-2-m {
    opacity: 0;
    transform: translateY(150px);
}

.flyup-1-m {
    opacity: 1;
    transform: translateY(0px);
}

.flyup-2-m {
    opacity: 0;
    transform: translateY(-150px);
}

.invert {
    filter: invert(1);
}

.unselectable {
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.testing-text {
    font-size: 3rem;
}