.text-box-hover {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 0;
    background-color: white;
}

.text-box-hover-active {
    height: 100%;
}

.text-box-link-text {
    mix-blend-mode: difference;
    font-family: 'Chivo Mono', monospace;
}

.text-box-underline {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: white;
}

.text-box-underline-active {
    background-color: black;
    bottom: 2px;
}