.text-box-container {
    position: relative;
    width: max-content;
    height: max-content;
}

.text-box-text-hide {
        display: none;
}

.text-box-container-transition-1, .text-box-container-transition-2 {
    position: absolute;
}

.text-box-container-transition-1 {
    background-color: black;
}

.text-box-container-transition-2 {
    background-color: white;
}

.text-box-open-intro {
    top: 0;
    right: 0;
    width: 0;
    height: 100%;
}

.text-box-open-middle {
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

.text-box-open-outro {
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
}

.text-box-close-intro {
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
}

.text-box-text {
    white-space: pre-line;
}

.text-box-close-middle {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.text-box-close-outro {
    top: 0;
    right: 0;
    width: 0;
    height: 100%;
}

.text-box-transition {
    transition: all 300ms cubic-bezier(0.765, -0.005, 0.330, 1.000); /* custom */
    transition-timing-function: cubic-bezier(0.765, -0.005, 0.330, 1.000); /* custom */
}