#root {
    background: black;
}

body {
    overflow-x: hidden;
    background: black !important;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: #ffffff rgba(255, 255, 255, 0);
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
    width: 0;
}

*::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}

*::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    border-radius: 10px;
    border: px none rgba(255, 255, 255, 0);
}

* {
    color: white;
}

.home-big-container-owo {
    background: black;
    position: absolute;
    top: 70px;
    left: 0;
    right: 0;
    bottom: 100px;
}

.home-big-container {
    position: relative;
    width: 100%;
    height: 100%;
}

.footer-content {
    display: flex;
    justify-content: space-between;
}

.footer-content-link {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 10px;
}

.loading-footer, .loading-footer-1 {
    background-color: black;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 0;
    z-index: 50;
    overflow: hidden;
    box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.5);
}

.loading-footer svg {
    transform: scale(1);
    position: absolute;
    width: 100%;
    height: 100px;
    margin-bottom: -7px;
    min-height: 100px;
    max-height: 150px;
    opacity: .4;
    left: 0;
}

.loading-footer svg g use {
    fill: none;
    stroke: rgba(194, 194, 194, 0.69);
    stroke-width: .5px;
}

.loading-footer-1 {
    background-color: #414141;
}

.loading-footer-s {
    height: 100px;
    padding: 10px;
}

.loading-box {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 50;
    background: black;
    padding: 10px;
    display: flex;
    justify-content: space-between;
}

.loading-box-m {
    height: 75px;
    box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.5);
}

.loading-text-box-h {
    opacity: 0;
    position: absolute;
    margin-left: 200px;
}

loading-text-box-m {
    opacity: 1;
    margin-right: 0px;
}

.loading-text {
    width: max-content;
    height: max-content;
    position: absolute;
    left: 70px;
    top: 5px;
}

.loading-text {
    font-family: 'Zen Dots', cursive;
    font-size: 30px;
}

.loading-bar-container div {
    position: relative !important;
    height: 5px !important;
}

.avatar-loading-box {
    width: 200px;
    height: 200px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.avatar-loading-box-m {
    top: 5px;
    left: 5px;
    width: 65px;
    height: 65px;
    transform: none;
}

.language-selector {
    max-height: 60px;
    height: max-content;
    width: 150px;
    overflow: hidden;
    border: 1px solid transparent;
    opacity: 0;
    box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.5);
}

@keyframes show {
    0% {
        display: block;
        opacity: 0;
    }

    5% {
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

@keyframes hide {
    0% {
        display: block;
    }

    95% {
        opacity: 0;
    }

    100% {
        display: none;
        opacity: 0;
    }
}

.language-selector-show {
    animation: show 0.5s ease-in-out forwards;
    pointer-events: auto;
}

.language-selector-hide {
    animation: hide 0.5s ease-in-out forwards;
    pointer-events: none;
}

.language-selector:hover {
        max-height: 150px;
        border: 1px solid white;
        box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.5);
}

.language-selector-child {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    user-select: none;
    cursor: pointer;
    background-color: black;
    padding: 5px 10px;
}

.language-selector-child:first-child:hover {
    cursor: not-allowed;
}

.language-selector-child:not(:first-child) {
    border-top: 1px solid #808080;
}

.language-selector-child:not(:first-child):hover {
    background-color: #414141;
}

.language-selector-child img {
    height: 30px;
    width: 30px;
    object-fit: cover;
    border-radius: 50%;
}

.language-selector-child p {
    margin-left: 10px;
    font-size: 15px;
    font-family: 'Zen Dots', cursive;
}

.loading-box-avatar {
    border-radius: 50%;
    position: absolute;
}

.loading-box-avatar-m {
    border-radius: 10%;
}

.brand-loading-image {
    width: 200px;
    opacity: 0;
}

.display-none {
    display: none !important;
}

.flydown-1 {
    opacity: 0;
    top: -100px;
}

.flydown-2 {
    opacity: 1;
    top: 0;
}

.hide {
    opacity: 0;
}

.show {
    opacity: 1;
}

.transition {
    transition: all 800ms cubic-bezier(0.915, 0.015, 0.240, 1.000); /* custom */
    transition-timing-function: cubic-bezier(0.915, 0.015, 0.240, 1.000); /* custom */
}

.bar-transition {
    transition: all 2000ms cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
    transition-timing-function: cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
}

.rotate-bar-transition {
    transition: transform 2000ms cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
    transition-timing-function: cubic-bezier(0.795, 0.000, 0.345, 0.995); /* custom */
}

.three-hundred-ms-transition {
    transition: all 300ms cubic-bezier(0.395, 0.010, 0.015, 1.010); /* custom */
    transition-timing-function: cubic-bezier(0.395, 0.010, 0.015, 1.010); /* custom */
}

.normal-transition {
    transition: all 800ms cubic-bezier(0.250, 0.460, 0.450, 0.940); /* custom */
    transition-timing-function: cubic-bezier(0.250, 0.460, 0.450, 0.940); /* custom */
}

.short-transition {
    transition: all 400ms cubic-bezier(0.250, 0.460, 0.450, 0.940); /* custom */
    transition-timing-function: cubic-bezier(0.250, 0.460, 0.450, 0.940); /* custom */
}

.video-transition {
    transition: all 300ms cubic-bezier(0.250, 0.460, 0.450, 0.940); /* custom */
    transition-timing-function: cubic-bezier(0.250, 0.460, 0.450, 0.940); /* custom */
}

.right {
    text-align: right;
    width: 100%;
}

.stack {
    display: grid;
    grid-template-columns: 1fr;
    overflow: hidden;
}

.stack span {
    font-weight: bold;
    grid-row-start: 1;
    grid-column-start: 1;
    font-size: 3rem;
    --stack-height: calc(100% / var(--stacks) - 1px);
    --inverse-index: calc(calc(var(--stacks) - 1) - var(--index));
    --clip-top: calc(var(--stack-height) * var(--index));
    --clip-bottom: calc(var(--stack-height) * var(--inverse-index));
    clip-path: inset(var(--clip-top) 0 var(--clip-bottom) 0);
    animation: stack 540ms cubic-bezier(.46, .29, 0, 1.24) 1 backwards calc(var(--index) * 120ms), glitch 1s ease infinite 1s alternate-reverse;
}

.stack span:nth-child(odd) {
    --glitch-translate: 8px;
}

.stack span:nth-child(even) {
    --glitch-translate: -8px;
}

@keyframes stack {
    0% {
        opacity: 0;
        transform: translateX(-50%);
        text-shadow: -2px 3px 0 red, 2px -3px 0 blue;
    }
    60% {
        opacity: 0.5;
        transform: translateX(50%);
    }
    80% {
        transform: none;
        opacity: 1;
        text-shadow: 2px -3px 0 red, -2px 3px 0 blue;
    }
    100% {
        text-shadow: none;
    }
}

@keyframes glitch {
    0% {
        text-shadow: -2px 3px 0 red, 2px -3px 0 blue;
        transform: translate(var(--glitch-translate));
    }
    2% {
        text-shadow: 2px -3px 0 red, -2px 3px 0 blue;
    }
    4%, 100% {
        text-shadow: none;
        transform: none;
    }
}

.glitch2 {
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;
    position: relative;
    text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff,
    0.025em 0.04em 0 #fffc00;
    animation: glitch 725ms infinite;
}

.glitch2 span {
    position: absolute;
    top: 0;
    left: 0;
}

.glitch2 span:first-child {
    animation: glitch21 500ms infinite;
    clip-path: polygon(0 0, 100% 0, 100% 35%, 0 35%);
    transform: translate(-0.04em, -0.03em);
    opacity: 0.75;
}

.glitch2 span:last-child {
    animation: glitch21 375ms infinite;
    clip-path: polygon(0 65%, 100% 65%, 100% 100%, 0 100%);
    transform: translate(0.04em, 0.03em);
    opacity: 0.75;
}

@keyframes glitch21 {
    0% {
        text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff,
        0.025em 0.04em 0 #fffc00;
    }
    15% {
        text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff,
        0.025em 0.04em 0 #fffc00;
    }
    16% {
        text-shadow: -0.05em -0.025em 0 #00fffc, 0.025em 0.035em 0 #fc00ff,
        -0.05em -0.05em 0 #fffc00;
    }
    49% {
        text-shadow: -0.05em -0.025em 0 #00fffc, 0.025em 0.035em 0 #fc00ff,
        -0.05em -0.05em 0 #fffc00;
    }
    50% {
        text-shadow: 0.05em 0.035em 0 #00fffc, 0.03em 0 0 #fc00ff,
        0 -0.04em 0 #fffc00;
    }
    99% {
        text-shadow: 0.05em 0.035em 0 #00fffc, 0.03em 0 0 #fc00ff,
        0 -0.04em 0 #fffc00;
    }
    100% {
        text-shadow: -0.05em 0 0 #00fffc, -0.025em -0.04em 0 #fc00ff,
        -0.04em -0.025em 0 #fffc00;
    }
}

.parallax > use {
    animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax > use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
}

.parallax > use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
}

.parallax > use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
}

.parallax > use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }
    100% {
        transform: translate3d(85px, 0, 0);
    }
}

/*Shrinking for mobile*/
@media (max-width: 768px) {
    .waves {
        height: 40px;
        min-height: 40px;
    }
}