@import url('https://fonts.googleapis.com/css2?family=Arimo:wght@500&family=Chivo+Mono:wght@600&family=Kanit:wght@400;500;600&family=Noto+Serif+JP&family=Orbitron:wght@700&family=Rock+Salt&family=Russo+One&family=VT323&family=Zen+Dots&display=swap');

body {
    padding: 0;
    margin: 0;
}
.confirmation-en {
    font-family: 'Chivo Mono', monospace;
    font-weight: bold;
}

.confirmation-ja {
    font-family: 'Noto Serif JP', serif;
    font-size: .8rem;
}

.confirmation {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90vw;
    padding: 10px;
}

   .confirmation-button {
    background-color: #23a6d5;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 0 10px;
    font-size: 1rem;
    font-family: 'Chivo Mono', monospace;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
       margin: 60px 0;
    }

    .confirmation-button:active {
    background-color: #23a6d5;
    color: #fff;
    }

.main-mobile-view {
    background-color: #0c0c0c;
    height: 100vh;
    width: 100vw;
    font-family: 'VT323', monospace;
}

.main-mobile-view p {
    margin: 0 10px;
}

.input-text {
    height: max-content;
    display: flex;
    /*background-color: #00fffc;*/
}

.input-text-prompt {
    color: lawngreen;
    margin-right: 5px !important;
}

.input-text form {
    height: 100%;
    width: 100%;
    padding: 0;
}

.input-text form input {
    height: 100%;
    width: 100%;
    padding: 0;
    border: none;
    background-color: transparent;
    color: white;
    font-family: 'VT323', monospace;
    font-size: 1rem;
}

.input-text form input:focus {
    outline: none;
}

.input-text form input::placeholder {
    color: #00fffc;
}

.red {
    color: red;
}