.text-logo-container {
}

.text-logo-container-top {
    width: max-content;
    height: 45px;
    padding: 0 10px;
    position: relative;
}

.text-logo-box-top {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: white;
}

.text-logo-box-top-active {
    height: 100%;
}

.text-logo-box-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: white;
}

.text-logo-box-bottom-active {
    height: 100%;
}

.text-logo-top {
    font-family: 'Orbitron', sans-serif;
    font-size: 30px;
}

.text-logo-bottom {
    font-family: 'Arimo', sans-serif;
    font-size: 17px;
    text-align: center;
    width: 100%;
    padding-left: 7px;
    animation: bottomTextTransition 1s ease-in-out forwards;
}

@keyframes bottomTextTransition {
    0% {
        opacity: 0;
        transform: translateY(-100%);
        letter-spacing: 0;
    }

    100% {
        opacity: 1;
        transform: translateY(0);
        letter-spacing: 7px;
    }
}